@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
body{
    margin: 0;
    padding: 0;
    font-family: 'Noto Sans JP',
    'Lato',
    '游ゴシック Medium',
    '游ゴシック体',
    'Yu Gothic Medium',
    'YuGothic',
    'ヒラギノ角ゴ ProN',
    'Hiragino Kaku Gothic ProN',
    'メイリオ',
    'Meiryo',
    'ＭＳ Ｐゴシック',
    'MS PGothic',
    'sans-serif';
}
.loadBox{
    height: 100vh;
}

@supports (-webkit-touch-callout: none) {
    .loadBox {
      /* Safari用のハック */
      height: -webkit-fill-available;
    }
}
.header_logo{
    font-family: 'Fredoka One', cursive;
    font-weight: bold;
    letter-spacing: 1px;
    position: relative;
    color: #55E6C1;
    z-index: 2;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    box-sizing: border-box;
    padding-left: 8px;
}
